import { motion, useCycle, Variants } from "framer-motion";
import React, { useRef } from "react";
import { NavLink } from "react-router-dom";
import { useDimensions } from "../../utils/useDimention";
import { MenuToggle } from "./Menu";

const sidebar: Variants = {
  open: (height = 1000) => ({
    clipPath: `circle(${height * 2 + 200}px at 100% 40px)`,
    transition: {
      type: "spring",
      stiffness: 20,
      restDelta: 2,
    },
  }),
  closed: {
    clipPath: `circle(0px at 100% 40px)`,
    transition: {
      delay: 0.2,
      type: "spring",
      stiffness: 400,
      damping: 40,
    },
  },
};

const Header: React.FC = () => {
  const containerRef = useRef<HTMLElement>(null);
  const { height } = useDimensions(containerRef);
  const [isOpen, toggleOpen] = useCycle(false, true);

  return (
    <nav className="w-full max-w-5xl mx-auto">
      <motion.nav
        initial={{ opacity: 0, y: -100 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          type: "spring",
          delay: 0.6,
          duration: 0.8,
        }}
        className="w-full flex items-center justify-between p-6 h-20 gap-2"
      >
        <div className="font-logo text-black text-2xl">
          <NavLink to="/">Home</NavLink>
        </div>
        <div className="items-center justify-between gap-5 hidden sm:flex">
          <NavLink
            to="/"
            className={({ isActive }) =>
              isActive ? "text-black text-xl" : "text-gray-600 text-xl"
            }
          >
            About
          </NavLink>
          <NavLink
            to="/projects"
            className={({ isActive }) =>
              isActive ? "text-black text-xl" : "text-gray-600 text-xl"
            }
          >
            Projects
          </NavLink>
          <NavLink
            to="/contact"
            className={({ isActive }) =>
              isActive ? "text-black text-xl" : "text-gray-600 text-xl"
            }
          >
            Contact
          </NavLink>
          <NavLink
            to="/blog"
            className={({ isActive }) =>
              isActive ? "text-black text-xl" : "text-gray-600 text-xl"
            }
          >
            Blog
          </NavLink>
        </div>

        <motion.nav
          initial={false}
          animate={isOpen ? "open" : "closed"}
          custom={height}
          ref={containerRef}
          className="sm:hidden"
        >
          <motion.div
            className="fixed inset-0 flex items-center justify-center bg-white backdrop-blur z-20 overflow-hidden"
            variants={sidebar}
          >
            <div className="flex items-center justify-around flex-col gap-4">
              <NavLink
                onClick={() => toggleOpen()}
                to="/"
                className={({ isActive }) =>
                  isActive ? "text-black text-xl" : "text-gray-600 text-xl"
                }
              >
                About
              </NavLink>
              <NavLink
                onClick={() => toggleOpen()}
                to="/projects"
                className={({ isActive }) =>
                  isActive ? "text-black text-xl" : "text-gray-600 text-xl"
                }
              >
                Projects
              </NavLink>
              <NavLink
                to="/contact"
                onClick={() => toggleOpen()}
                className={({ isActive }) =>
                  isActive ? "text-black text-xl" : "text-gray-600 text-xl"
                }
              >
                Contact
              </NavLink>
              <NavLink
                to="/blog"
                onClick={() => toggleOpen()}
                className={({ isActive }) =>
                  isActive ? "text-black text-xl" : "text-gray-600 text-xl"
                }
              >
                Blog
              </NavLink>
            </div>
          </motion.div>
          <MenuToggle toggle={() => toggleOpen()} />
        </motion.nav>
      </motion.nav>
    </nav>
  );
};

export default Header;
